import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Card,
  CardContent,
  Typography,
  Button,
  Box,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  CircularProgress,
  Alert,
  Snackbar,
  IconButton,
  Menu,
  MenuItem,
  Divider,
  Dialog as ConfirmDialog,
  DialogContentText
} from '@mui/material';
import { useProjectContext } from '../context/ProjectContext';
import AddIcon from '@mui/icons-material/Add';
import FolderIcon from '@mui/icons-material/Folder';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import { useLoading } from '../context/LoadingContext';

const ProjectsPage: React.FC = () => {
  const navigate = useNavigate();
  const [openDialog, setOpenDialog] = useState(false);
  const [newProject, setNewProject] = useState({ name: '', description: '' });
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' as 'success' | 'error' });
  const [contextMenu, setContextMenu] = useState<{
    mouseX: number;
    mouseY: number;
    projectId: string | null;
  } | null>(null);
  const [confirmDelete, setConfirmDelete] = useState<{
    open: boolean;
    projectId: string | null;
    projectName: string;
    isDeleting?: boolean;
  }>({
    open: false,
    projectId: null,
    projectName: ''
  });
  const [editProject, setEditProject] = useState<{
    open: boolean;
    projectId: string | null;
    name: string;
    description: string;
    isSubmitting: boolean;
  }>({
    open: false,
    projectId: null,
    name: '',
    description: '',
    isSubmitting: false
  });

  // ProjectContext'ten verileri ve fonksiyonları al
  const { 
    projects, 
    loading, 
    error, 
    loadProjects, 
    createProject,
    deleteProject,
    updateProject
  } = useProjectContext();
  
  // Global loading state
  const { setLoading } = useLoading();

  // Sayfa yüklendiğinde projeleri yükle
  useEffect(() => {
    const fetchProjects = async () => {
      try {
        setLoading(true);
        await loadProjects();
      } catch (err) {
        console.error('Projeler yüklenirken hata oluştu:', err);
      } finally {
        setLoading(false);
      }
    };
    
    fetchProjects();
    // useEffect'in sadece bir kez çalışması için boş dependency array
  }, [loadProjects, setLoading]);

  const handleCreateProject = async () => {
    if (!newProject.name.trim()) {
      return;
    }
    
    try {
      setLoading(true);
      const result = await createProject({
        name: newProject.name,
        description: newProject.description,
      });
      
      if (result) {
        console.log('Yeni proje oluşturuldu:', result);
        
        const projectId = result.id;
        console.log('Proje ID:', projectId);
        
        setOpenDialog(false);
        setNewProject({ name: '', description: '' });
        setSnackbar({ 
          open: true, 
          message: 'Proje başarıyla oluşturuldu!', 
          severity: 'success' 
        });
        
        // Proje oluşturulduktan sonra otomatik olarak yeni projeye yönlendir
        if (projectId) {
          setTimeout(() => {
            navigate(`/projects/${projectId}`);
          }, 1000);
        } else {
          console.error('Proje oluşturuldu ancak ID bulunamadı:', result);
        }
      }
    } catch (err) {
      console.error('Proje oluşturulurken hata oluştu:', err);
      setSnackbar({ 
        open: true, 
        message: 'Proje oluşturulurken bir hata oluştu.', 
        severity: 'error' 
      });
    } finally {
      setLoading(false);
    }
  };

  const handleOpenMenu = (event: React.MouseEvent<HTMLButtonElement>, projectId: string) => {
    event.stopPropagation();
    setContextMenu({
      mouseX: event.clientX,
      mouseY: event.clientY,
      projectId
    });
  };

  const handleCloseMenu = () => {
    setContextMenu(null);
  };

  const handleDeleteProject = async () => {
    if (!confirmDelete.projectId) return;
    
    try {
      setConfirmDelete({...confirmDelete, isDeleting: true });
      setLoading(true);
      
      console.log('Proje silme işlemi başlatılıyor, ID:', confirmDelete.projectId);
      
      try {
        const result = await deleteProject(confirmDelete.projectId);
        console.log('Proje silme sonucu:', result);
        
        if (result) {
          // Projeleri yeniden yükle
          console.log('Projeler yeniden yükleniyor...');
          await loadProjects();
          
          setSnackbar({
            open: true,
            message: `"${confirmDelete.projectName}" projesi başarıyla silindi`,
            severity: 'success'
          });
        } else {
          setSnackbar({
            open: true,
            message: 'Proje silinemedi, lütfen tekrar deneyin',
            severity: 'error'
          });
        }
      } catch (error: any) {
        console.error('Proje silme API hatası:', error);
        
        // Token/yetkilendirme hatası değilse, sadece hata mesajı göster login'e atma
        let errorMessage = 'Proje silinirken bir hata oluştu';
        
        if (error.response?.status === 401) {
          errorMessage = 'Oturum süreniz dolmuş, lütfen tekrar giriş yapın';
          // Oturum hatası durumunda bile kullanıcıyı rahatsız etmemek için 
          // navigate('/login') çağrısını kaldırıyoruz
        }
        
        setSnackbar({
          open: true,
          message: errorMessage,
          severity: 'error'
        });
      }
    } catch (err) {
      console.error('Proje silme işlemi başarısız oldu:', err);
      setSnackbar({
        open: true,
        message: 'Proje silinirken beklenmeyen bir hata oluştu',
        severity: 'error'
      });
    } finally {
      setConfirmDelete({
        open: false,
        projectId: null,
        projectName: '',
        isDeleting: false
      });
      setLoading(false);
    }
  };

  const openDeleteConfirm = (projectId: string) => {
    const project = projects.find(p => p.id === projectId);
    if (project) {
      setConfirmDelete({
        open: true,
        projectId,
        projectName: project.name
      });
    }
    handleCloseMenu();
  };

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  const handleEditProject = async () => {
    if (!editProject.projectId || !editProject.name.trim()) {
      return;
    }
    
    try {
      setEditProject({ ...editProject, isSubmitting: true });
      setLoading(true);
      
      const result = await updateProject(editProject.projectId, {
        name: editProject.name,
        description: editProject.description
      });
      
      if (result) {
        setEditProject({
          open: false,
          projectId: null,
          name: '',
          description: '',
          isSubmitting: false
        });
        
        // Projeleri güncelleme sonrası yeniden yükle
        await loadProjects();
        
        setSnackbar({
          open: true,
          message: 'Proje başarıyla güncellendi!',
          severity: 'success'
        });
      }
    } catch (err) {
      console.error('Proje güncellenirken hata oluştu:', err);
      setSnackbar({
        open: true,
        message: 'Proje güncellenirken bir hata oluştu.',
        severity: 'error'
      });
      setEditProject({ ...editProject, isSubmitting: false });
    } finally {
      setLoading(false);
    }
  };

  const openEditDialog = (projectId: string) => {
    const project = projects.find(p => p.id === projectId);
    if (project) {
      setEditProject({
        open: true,
        projectId: project.id,
        name: project.name,
        description: project.description || '',
        isSubmitting: false
      });
    }
    handleCloseMenu();
  };

  if (loading) {
    return <></>;
  }

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 8 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 4 }}>
        <Typography variant="h4">Projelerim</Typography>
        <Button
          variant="contained"
          color="primary"
          startIcon={<AddIcon />}
          onClick={() => setOpenDialog(true)}
        >
          Yeni Proje
        </Button>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      {projects.length === 0 ? (
        <Box 
          sx={{ 
            display: 'flex', 
            flexDirection: 'column', 
            alignItems: 'center', 
            justifyContent: 'center',
            py: 8
          }}
        >
          <FolderIcon sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} />
          <Typography variant="h6" color="text.secondary" gutterBottom>
            Henüz projeniz bulunmamaktadır
          </Typography>
          <Typography color="text.secondary" align="center" sx={{ maxWidth: 500, mb: 3 }}>
            Yeni bir proje oluşturarak görevlerinizi düzenlemeye başlayabilirsiniz.
          </Typography>
          <Button 
            variant="contained" 
            startIcon={<AddIcon />}
            onClick={() => setOpenDialog(true)}
          >
            İlk Projeni Oluştur
          </Button>
        </Box>
      ) : (
        <Box sx={{ display: 'grid', gridTemplateColumns: { xs: '1fr', sm: '1fr 1fr', md: '1fr 1fr 1fr' }, gap: 3 }}>
          {projects.map((project) => (
            <Box 
              key={project.id}
              sx={{ width: '100%' }}
            >
              <Card 
                sx={{ 
                  height: '100%', 
                  display: 'flex', 
                  flexDirection: 'column',
                  position: 'relative',
                  cursor: 'pointer',
                  transition: 'all 0.2s',
                  border: '1px solid rgba(0,0,0,0.08)',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: 3,
                    borderColor: 'primary.light'
                  }
                }}
              >
                <IconButton
                  sx={{ 
                    position: 'absolute', 
                    top: 8, 
                    right: 8,
                    backgroundColor: 'rgba(255,255,255,0.8)',
                    '&:hover': {
                      backgroundColor: 'rgba(255,255,255,0.9)'
                    }
                  }}
                  onClick={(e) => handleOpenMenu(e, project.id)}
                >
                  <MoreVertIcon fontSize="small" />
                </IconButton>
                <CardContent 
                  onClick={(e) => {
                    // Menü düğmesine tıklandıysa yönlendirmeyi engelle
                    if ((e.target as Element).closest('button')) {
                      return;
                    }
                    navigate(`/projects/${project.id}`)
                  }}
                  sx={{ pt: 3, pb: 2, height: '100%' }}
                >
                  <Typography variant="h6" gutterBottom noWrap>
                    {project.name}
                  </Typography>
                  <Typography 
                    color="text.secondary" 
                    sx={{ 
                      mb: 2, 
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                      display: '-webkit-box',
                      WebkitLineClamp: 3,
                      WebkitBoxOrient: 'vertical'
                    }}
                  >
                    {project.description}
                  </Typography>
                  <Box sx={{ 
                    display: 'flex', 
                    justifyContent: 'space-between', 
                    alignItems: 'center',
                    mt: 'auto'
                  }}>
                    <Typography variant="body2" color="text.secondary">
                      {project.tasks?.length || 0} görev
                    </Typography>
                    <Typography variant="caption" color="text.secondary">
                      {new Date(project.createdAt).toLocaleDateString('tr-TR')}
                    </Typography>
                  </Box>
                </CardContent>
              </Card>
            </Box>
          ))}
        </Box>
      )}

      {/* Context Menü */}
      <Menu
        open={contextMenu !== null}
        onClose={handleCloseMenu}
        anchorReference="anchorPosition"
        anchorPosition={
          contextMenu !== null
            ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
            : undefined
        }
      >
        <MenuItem
          dense
          onClick={() => {
            if (contextMenu?.projectId) {
              openEditDialog(contextMenu.projectId);
            }
          }}
        >
          <EditIcon fontSize="small" sx={{ mr: 1 }} />
          Düzenle
        </MenuItem>
        <Divider />
        <MenuItem 
          dense
          onClick={() => contextMenu?.projectId && openDeleteConfirm(contextMenu.projectId)}
          sx={{ color: 'error.main' }}
        >
          <DeleteIcon fontSize="small" sx={{ mr: 1 }} />
          Sil
        </MenuItem>
      </Menu>

      {/* Yeni Proje Dialogu */}
      <Dialog 
        open={openDialog} 
        onClose={() => setOpenDialog(false)}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>Yeni Proje Oluştur</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Proje Adı"
            fullWidth
            value={newProject.name}
            onChange={(e) =>
              setNewProject({ ...newProject, name: e.target.value })
            }
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Açıklama"
            fullWidth
            multiline
            rows={4}
            value={newProject.description}
            onChange={(e) =>
              setNewProject({ ...newProject, description: e.target.value })
            }
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>İptal</Button>
          <Button 
            onClick={handleCreateProject} 
            variant="contained"
            disabled={!newProject.name.trim()}
          >
            Oluştur
          </Button>
        </DialogActions>
      </Dialog>

      {/* Proje Düzenleme Dialogu */}
      <Dialog
        open={editProject.open} 
        onClose={() => !editProject.isSubmitting && setEditProject({...editProject, open: false})}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>Projeyi Düzenle</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Proje Adı"
            fullWidth
            value={editProject.name}
            onChange={(e) =>
              setEditProject({ ...editProject, name: e.target.value })
            }
            disabled={editProject.isSubmitting}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Açıklama"
            fullWidth
            multiline
            rows={4}
            value={editProject.description}
            onChange={(e) =>
              setEditProject({ ...editProject, description: e.target.value })
            }
            disabled={editProject.isSubmitting}
          />
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => setEditProject({...editProject, open: false})}
            disabled={editProject.isSubmitting}
          >
            İptal
          </Button>
          <Button 
            onClick={handleEditProject} 
            variant="contained"
            disabled={!editProject.name.trim() || editProject.isSubmitting}
            startIcon={editProject.isSubmitting ? <CircularProgress size={20} color="inherit" /> : null}
          >
            {editProject.isSubmitting ? 'Güncelleniyor...' : 'Güncelle'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Silme Onay Dialogu */}
      <ConfirmDialog
        open={confirmDelete.open}
        onClose={() => !confirmDelete.isDeleting && setConfirmDelete({...confirmDelete, open: false})}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Projeyi silmek istediğinizden emin misiniz?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <strong>"{confirmDelete.projectName}"</strong> projesini silmek üzeresiniz. 
            Bu işlem geri alınamaz ve projeye ait tüm görevler de silinecektir.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => setConfirmDelete({...confirmDelete, open: false})} 
            color="primary"
            disabled={confirmDelete.isDeleting}
          >
            İptal
          </Button>
          <Button 
            onClick={handleDeleteProject} 
            color="error" 
            variant="contained" 
            autoFocus
            disabled={confirmDelete.isDeleting}
            startIcon={confirmDelete.isDeleting ? <CircularProgress size={20} color="inherit" /> : null}
          >
            {confirmDelete.isDeleting ? 'Siliniyor...' : 'Sil'}
          </Button>
        </DialogActions>
      </ConfirmDialog>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity}
          variant="filled"
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default ProjectsPage; 