import React, { useState, useEffect } from 'react';
import { useNavigate, Link as RouterLink } from 'react-router-dom';
import {
  Container,
  Box,
  Typography,
  TextField,
  Button,
  Link,
  Alert,
  Paper,
  CircularProgress
} from '@mui/material';
import { useAuth } from '../context/AuthContext';
import { useLoading } from '../context/LoadingContext';

const RegisterPage: React.FC = () => {
  const navigate = useNavigate();
  const { register, error, loading, clearError, user } = useAuth();
  const { setLoading } = useLoading();
  const [formData, setFormData] = useState({
    name: '',
    surname: '',
    username: '',
    email: '',
    password: '',
    confirmPassword: ''
  });
  const [validationError, setValidationError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);
  
  // Kullanıcı oturum açtığında yönlendirme yapma
  useEffect(() => {
    if (user) {
      navigate('/projects');
    }
  }, [user, navigate]);
  
  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    
    // Validasyon hatasını temizle
    setValidationError(null);
  };
  
  const validateForm = (): boolean => {
    if (formData.password !== formData.confirmPassword) {
      setValidationError('Şifreler eşleşmiyor');
      return false;
    }
    
    if (formData.password.length < 6) {
      setValidationError('Şifre en az 6 karakter olmalıdır');
      return false;
    }
    
    if (!formData.email.endsWith('@gmail.com')) {
      setValidationError('Sadece Gmail hesapları kabul edilmektedir');
      return false;
    }
    
    return true;
  };
  
  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    clearError();
    setValidationError(null);
    
    // Form doğrulama
    if (!validateForm()) {
      return;
    }
    
    setIsSubmitting(true);
    setLoading(true);
    
    try {
      await register(formData);
    } catch (err) {
      console.error('Kayıt sırasında hata oluştu:', err);
    } finally {
      setIsSubmitting(false);
      setLoading(false);
    }
  };
  
  // Local loading durumu ve genel loading durumunu birleştir
  const isLoading = loading || isSubmitting;
  // Görüntülenecek hata (validasyon hatası veya API hatası)
  const displayError = validationError || error;
  
  return (
    <Container maxWidth="sm" sx={{ mt: 4, mb: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5" gutterBottom>
            Hesap Oluştur
          </Typography>
          
          {displayError && (
            <Alert severity="error" sx={{ mt: 2, width: '100%' }}>
              {displayError}
            </Alert>
          )}
          
          <Box component="form" onSubmit={handleRegister} sx={{ mt: 3, width: '100%' }}>
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 2 }}>
              <Box sx={{ flex: '1 1 45%' }}>
                <TextField
                  required
                  fullWidth
                  id="name"
                  label="Adınız"
                  name="name"
                  autoComplete="given-name"
                  value={formData.name}
                  onChange={handleChange}
                  disabled={isLoading}
                />
              </Box>
              <Box sx={{ flex: '1 1 45%' }}>
                <TextField
                  required
                  fullWidth
                  id="surname"
                  label="Soyadınız"
                  name="surname"
                  autoComplete="family-name"
                  value={formData.surname}
                  onChange={handleChange}
                  disabled={isLoading}
                />
              </Box>
              <Box sx={{ width: '100%', mt: 2 }}>
                <TextField
                  required
                  fullWidth
                  id="username"
                  label="Kullanıcı Adı"
                  name="username"
                  autoComplete="username"
                  value={formData.username}
                  onChange={handleChange}
                  disabled={isLoading}
                />
              </Box>
              <Box sx={{ width: '100%', mt: 2 }}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="E-posta Adresi"
                  name="email"
                  autoComplete="email"
                  helperText="Sadece Gmail hesapları kabul edilmektedir"
                  value={formData.email}
                  onChange={handleChange}
                  disabled={isLoading}
                  error={Boolean(validationError && validationError.includes('Gmail'))}
                />
              </Box>
              <Box sx={{ flex: '1 1 45%', mt: 2 }}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Şifre"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                  value={formData.password}
                  onChange={handleChange}
                  disabled={isLoading}
                  error={Boolean(validationError && validationError.includes('karakter'))}
                  helperText="En az 6 karakter"
                />
              </Box>
              <Box sx={{ flex: '1 1 45%', mt: 2 }}>
                <TextField
                  required
                  fullWidth
                  name="confirmPassword"
                  label="Şifre Tekrar"
                  type="password"
                  id="confirmPassword"
                  autoComplete="new-password"
                  value={formData.confirmPassword}
                  onChange={handleChange}
                  disabled={isLoading}
                  error={Boolean(validationError && validationError.includes('eşleşmiyor'))}
                />
              </Box>
            </Box>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2, height: 48 }}
              disabled={isLoading || 
                !formData.name.trim() || 
                !formData.surname.trim() || 
                !formData.username.trim() || 
                !formData.email.trim() || 
                !formData.password.trim() || 
                !formData.confirmPassword.trim()
              }
            >
              {isLoading ? (
                <CircularProgress size={24} color="inherit" />
              ) : (
                'Kayıt Ol'
              )}
            </Button>
            <Box sx={{ textAlign: 'center' }}>
              <Link component={RouterLink} to="/login" variant="body2">
                Zaten bir hesabınız var mı? Giriş yapın
              </Link>
            </Box>
          </Box>
        </Box>
      </Paper>
    </Container>
  );
};

export default RegisterPage; 