import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Paper,
  Divider,
  Alert,
  Chip,
  IconButton,
  Snackbar
} from '@mui/material';
import { DndContext, closestCorners, DragEndEvent } from '@dnd-kit/core';
import { restrictToWindowEdges } from '@dnd-kit/modifiers';
import { Task } from '../types';
import Todo from '../components/Todo';
import AddIcon from '@mui/icons-material/Add';
import AssignmentIcon from '@mui/icons-material/Assignment';
import { useTaskContext } from '../context/TaskContext';
import { useProjectContext } from '../context/ProjectContext';
import { useAuth } from '../context/AuthContext';
import { useLoading } from '../context/LoadingContext';
import { normalizeId, normalizeMongoData } from '../utils/dataUtils';

const ProjectDetailPage: React.FC = () => {
  const { projectId } = useParams<{ projectId: string }>();
  const navigate = useNavigate();
  const [openDialog, setOpenDialog] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: '',
    severity: 'success' as 'success' | 'error'
  });
  const [newTask, setNewTask] = useState({ 
    title: '', 
    description: '', 
    deadline: null as Date | null,
    priority: 'normal' as 'düşük' | 'normal' | 'yüksek',
    tags: [] as string[],
    status: 'yapiliyor' as Task['status']
  });
  const [initialStatus, setInitialStatus] = useState<Task['status']>('yapiliyor');
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isDataLoaded, setIsDataLoaded] = useState(false);
  const [errorShown, setErrorShown] = useState(false);
  const [previousProjectId, setPreviousProjectId] = useState<string | null>(null);
  const [editTask, setEditTask] = useState<Task | null>(null);
  
  // Taşıma işlemi için
  type PendingMove = { taskId: string; oldStatus: Task['status']; newStatus: Task['status'] };
  const [pendingMoves, setPendingMoves] = useState<PendingMove[]>([]);

  // Context'leri kullan
  const { 
    tasks, 
    loading: tasksLoading, 
    error: tasksError, 
    loadTasks, 
    addTask, 
    updateTask, 
    deleteTask,
    moveTask,
    clearTasks
  } = useTaskContext();
  
  const {
    selectedProject: project,
    loading: projectLoading,
    error: projectError,
    getProject,
    clearSelectedProject
  } = useProjectContext();
  
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const { user, checkTokenValidity } = useAuth();

  // Global loading state
  const { setLoading } = useLoading();

  // Hata gösterme fonksiyonu (sadece bir kez göster)
  const showErrorOnce = useCallback((message: string, severity: 'success' | 'error' = 'error') => {
    if (!errorShown) {
      setSnackbar({
        open: true,
        message,
        severity
      });
      setErrorShown(true);
    }
  }, [errorShown]);

  // TokenValidation fonksiyonu - API'ye erişim öncesi kontrol yapar
  const validateToken = useCallback(async (): Promise<boolean> => {
    try {
      return await checkTokenValidity();
    } catch (err) {
      console.error('Token kontrolü sırasında hata:', err);
      return false;
    }
  }, [checkTokenValidity]);

  // Proje sahibi kontrolü
  // Her kullanıcının tüm işlemleri yapabilmesi için sahibi true olarak ayarlıyoruz
  const isProjectOwner = true;

  // Token validasyonu ve oturum kontrolü
  useEffect(() => {
    const validateSession = async () => {
      try {
        const isValid = await validateToken();
        if (!isValid) {
          navigate('/login');
        }
      } catch (err) {
        console.error('Oturum doğrulama hatası:', err);
      }
    };
    
    validateSession();
  }, [validateToken, navigate]);

  // ProjectId değiştiğinde verileri temizle ve yeniden yükle
  useEffect(() => {
    if (projectId !== previousProjectId) {
      console.log('Proje değişti, veri temizleniyor:', previousProjectId, '->', projectId);
      
      // Global loader'ı etkinleştir
      setLoading(true);
      
      // Mevcut verileri temizle
      clearSelectedProject();
      clearTasks();
      
      // Veri yükleme durumunu sıfırla
      setIsDataLoaded(false);
      setErrorShown(false);
      
      // Yeni ProjectId'yi kaydet
      setPreviousProjectId(projectId || null);
    }
  }, [projectId, previousProjectId, clearSelectedProject, clearTasks, setLoading]);

  // Proje ve görevleri yükleme
  useEffect(() => {
    const fetchData = async () => {
      if (!projectId) {
        showErrorOnce('Geçersiz proje ID');
        navigate('/projects');
        return;
      }
      
      try {
        // Token kontrolü
        const isTokenValid = await validateToken();
        if (!isTokenValid) {
          showErrorOnce('Oturum süresi doldu, lütfen tekrar giriş yapın');
          navigate('/login');
          return;
        }
        
        setLoading(true);
        setIsDataLoaded(false);
        
        // Önce projeyi yükle
        const projectData = await getProject(projectId);
        
        if (!projectData) {
          showErrorOnce('Proje bulunamadı');
          navigate('/projects');
          return;
        }
        
        // Projeye ait görevleri yükle
        await loadTasks(projectId);
        
        setIsDataLoaded(true);
      } catch (err) {
        console.error('Proje detayları yüklenirken hata:', err);
        showErrorOnce('Proje detayları yüklenirken bir hata oluştu');
      } finally {
        setLoading(false);
      }
    };
    
    // Veri yükleme durumunu kontrol et
    if (!isDataLoaded && projectId) {
      fetchData();
    }
  }, [projectId, getProject, loadTasks, navigate, showErrorOnce, validateToken, setLoading, isDataLoaded]);

  const handleOpenDialog = (status: Task['status']) => {
    setInitialStatus(status);
    setNewTask({ 
      ...newTask, 
      status: status 
    });
    setOpenDialog(true);
  };

  const handleAddTask = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!projectId || !newTask.title.trim()) {
      setSnackbar({
        open: true,
        message: 'Görev başlığı boş olamaz',
        severity: 'error'
      });
      return;
    }
    
    try {
      setInitialStatus(newTask.status);
      setLoading(true);
      
      // Token geçerliliğini kontrol et
      const isTokenValid = await validateToken();
      if (!isTokenValid) {
        setSnackbar({
          open: true,
          message: 'Oturum süresi doldu, lütfen tekrar giriş yapın',
          severity: 'error'
        });
        navigate('/login');
        return;
      }
      
      console.log('Görev ekleme işlemi başlatılıyor...', newTask);
      const result = await addTask(projectId, {
        title: newTask.title,
        description: newTask.description,
        status: newTask.status,
        priority: newTask.priority,
        deadline: newTask.deadline || undefined,
        tags: newTask.tags
      });
      
      if (result) {
        console.log('Görev başarıyla eklendi:', result);
        setOpenDialog(false);
        setNewTask({
          title: '',
          description: '',
          deadline: null,
          priority: 'normal',
          tags: [],
          status: initialStatus
        });
        
        setSnackbar({
          open: true,
          message: 'Görev başarıyla eklendi!',
          severity: 'success'
        });
      } else {
        console.error('Görev ekleme başarısız oldu, null sonuç döndü');
        setSnackbar({
          open: true,
          message: 'Görev eklenirken bir hata oluştu.',
          severity: 'error'
        });
      }
    } catch (err) {
      console.error('Görev eklenirken hata oluştu:', err);
      setSnackbar({
        open: true,
        message: 'Görev eklenirken bir hata oluştu.',
        severity: 'error'
      });
    } finally {
      setLoading(false);
    }
  };

  // DnD için handleDragEnd fonksiyonu
  const handleDragEnd = useCallback((event: any) => {
    const { active, over } = event;
    
    if (!active || !over) return;
    
    const task = active.data?.current?.task;
    if (!task) {
      console.error('Taşınan görev bilgisi eksik:', active);
      return;
    }
    
    // Task ID'sini normalize et
    const taskId = normalizeId(task);
    
    const newStatus = over.id as Task['status'];
    if (task.status !== newStatus) {
      console.log(`Görev taşınıyor: ${taskId} (${task.status} -> ${newStatus})`);
      
      // İyimser UI güncelleme (optimistic update)
      setPendingMoves((prev) => [...prev, { taskId, oldStatus: task.status, newStatus }]);
      
      // Backend'e değişiklik isteği gönder
      moveTask(taskId, newStatus)
        .then((updatedTask) => {
          if (updatedTask) {
            console.log('Görev başarıyla taşındı:', updatedTask);
            setSnackbar({
              open: true,
              message: 'Görev durumu güncellendi',
              severity: 'success'
            });
          } else {
            console.error('Görev taşıma başarısız oldu');
            setSnackbar({
              open: true,
              message: 'Görev durumu güncellenemedi',
              severity: 'error'
            });
          }
        })
        .catch((err) => {
          console.error('Görev taşıma hatası:', err);
          setSnackbar({
            open: true,
            message: 'Görev durumu güncellenirken bir hata oluştu',
            severity: 'error'
          });
        })
        .finally(() => {
          // İşlem tamamlandığında pending moves'tan kaldır
          setPendingMoves((prev) => prev.filter((move) => move.taskId !== taskId));
        });
    }
  }, [moveTask]);

  // Özel görev silme fonksiyonu
  const handleDeleteTask = useCallback(async (taskId: string): Promise<boolean> => {
    try {
      console.log('ProjectDetailPage - handleDeleteTask çağrıldı, ID:', taskId);
      
      // TaskId parametresini kontrol et
      if (!taskId) {
        console.error('ProjectDetailPage - Görev silme işleminde geçersiz ID:', taskId);
        setSnackbar({
          open: true,
          message: 'Görev silme işlemi yapılamadı: Geçersiz görev ID',
          severity: 'error'
        });
        return false;
      }
      
      // ID'yi normalize et (MongoDB formatı sorunlarını önlemek için)
      const normalizedTaskId = normalizeId(taskId);
      console.log('ProjectDetailPage - Görev silme işlemi başlatılıyor (normalize):', normalizedTaskId);

      try {
        // Önce token geçerliliğini kontrol et
        console.log('ProjectDetailPage - Token kontrolü yapılıyor...');
        const isTokenValid = await validateToken();
        
        if (!isTokenValid) {
          console.error('ProjectDetailPage - Token geçersiz, görev silme işlemi iptal edildi');
          setSnackbar({
            open: true,
            message: 'Oturum süresi dolmuş, lütfen tekrar giriş yapın',
            severity: 'error'
          });
          return false;
        }
        
        console.log('ProjectDetailPage - Token geçerli, silme API çağrısı yapılıyor...');
      } catch (tokenError) {
        console.error('ProjectDetailPage - Token kontrolü sırasında hata:', tokenError);
        setSnackbar({
          open: true,
          message: 'Oturum kontrolü sırasında hata oluştu',
          severity: 'error'
        });
        return false;
      }

      // Görev silme işlemini çağır
      console.log('ProjectDetailPage - deleteTask context fonksiyonu çağrılıyor...');
      const result = await deleteTask(normalizedTaskId);
      console.log('ProjectDetailPage - deleteTask sonucu:', result);
      
      if (result) {
        console.log('ProjectDetailPage - Görev silme işlemi başarılı oldu');
        setSnackbar({
          open: true,
          message: 'Görev başarıyla silindi',
          severity: 'success'
        });
        return true;
      } else {
        console.error('ProjectDetailPage - Görev silme işlemi başarısız oldu');
        setSnackbar({
          open: true,
          message: 'Görev silinirken bir hata oluştu',
          severity: 'error'
        });
        return false;
      }
    } catch (error) {
      console.error('ProjectDetailPage - Görev silme hatası:', error);
      setSnackbar({
        open: true,
        message: 'Görev silme işlemi başarısız oldu: ' + (error instanceof Error ? error.message : 'Bilinmeyen hata'),
        severity: 'error'
      });
      return false;
    }
  }, [deleteTask, validateToken]);

  const loading = tasksLoading || projectLoading;
  const error = tasksError || projectError;

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  if (loading) {
    return <></>;
  }

  if (error) {
    return (
      <Container sx={{ mt: 3 }}>
        <Alert severity="error">{error}</Alert>
      </Container>
    );
  }

  // Proje bulunamadıysa
  if (!project) {
    return (
      <Container sx={{ mt: 5, textAlign: 'center' }}>
        <Alert severity="warning" sx={{ mb: 3 }}>
          Proje bulunamadı veya erişim izniniz yok.
        </Alert>
        <Button 
          variant="contained" 
          onClick={() => navigate('/projects')}
          sx={{ mt: 2 }}
        >
          Projeler Sayfasına Dön
        </Button>
      </Container>
    );
  }

  // Durum renkleri
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'yapiliyor':
        return '#f44336'; // Kırmızı
      case 'beklemede':
        return '#ffeb3b'; // Sarı
      case 'tamamlandi':
        return '#4caf50'; // Yeşil
      default:
        return '#e0e0e0';
    }
  };

  // Durum başlıkları
  const getStatusTitle = (status: string) => {
    switch (status) {
      case 'yapiliyor':
        return 'Yapılıyor';
      case 'beklemede':
        return 'Beklemede';
      case 'tamamlandi':
        return 'Tamamlandı';
      default:
        return status;
    }
  };

  // Öncelik renklerini belirle
  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'yüksek':
        return '#d32f2f'; // Koyu kırmızı
      case 'normal':
        return '#1976d2'; // Mavi
      case 'düşük':
        return '#388e3c'; // Yeşil
      default:
        return '#757575'; // Gri
    }
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 8 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Box>
          <Typography variant="h4" gutterBottom>
            {project?.name}
          </Typography>
          <Typography variant="body1" color="text.secondary">
            {project?.description}
          </Typography>
        </Box>
        <Box sx={{ display: 'flex', gap: 2 }}>
          {/* Projeler sayfasına dönüş butonu */}
          <Button
            variant="outlined"
            onClick={() => navigate('/projects')}
          >
            Tüm Projeler
          </Button>
          {/* Her kullanıcı için düğmeyi göster */}
          <Button
            variant="contained"
            color="primary"
            startIcon={<AddIcon />}
            onClick={() => handleOpenDialog('yapiliyor')}
          >
            Yeni Görev
          </Button>
        </Box>
      </Box>

      <DndContext 
        onDragEnd={handleDragEnd} 
        collisionDetection={closestCorners}
        modifiers={[restrictToWindowEdges]}
      >
        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, gap: 3 }}>
          {['yapiliyor', 'beklemede', 'tamamlandi'].map((status) => (
            <Box 
              key={status} 
              sx={{ 
                flex: 1, 
                width: '100%' 
              }}
            >
              <Paper 
                sx={{ 
                  p: 2, 
                  height: '100%', 
                  backgroundColor: status === 'yapiliyor' 
                    ? '#ffebee' // Açık kırmızı
                    : status === 'beklemede'
                    ? '#fffde7' // Açık sarı
                    : '#e8f5e9' // Açık yeşil
                }}
                elevation={1}
              >
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 2 }}>
                  <Chip 
                    label={getStatusTitle(status)}
                    sx={{ 
                      backgroundColor: getStatusColor(status),
                      color: status === 'beklemede' ? '#000' : '#fff',
                      fontWeight: 'bold',
                      px: 1
                    }}
                  />
                  
                  {/* Her kolona özel Görev Ekle butonu */}
                  <IconButton 
                    size="small" 
                    onClick={() => handleOpenDialog(status as Task['status'])}
                    sx={{ bgcolor: 'white', boxShadow: 1 }}
                  >
                    <AddIcon fontSize="small" />
                  </IconButton>
                </Box>
                
                <Divider sx={{ mb: 2 }} />
                
                <Box 
                  id={status} 
                  sx={{ 
                    minHeight: '60vh',
                    height: '100%',
                    p: 1
                  }}
                >
                  {tasks.filter((task) => task.status === status).length === 0 ? (
                    <Box 
                      sx={{ 
                        display: 'flex', 
                        flexDirection: 'column', 
                        alignItems: 'center', 
                        justifyContent: 'center',
                        height: '30vh',
                        opacity: 0.7
                      }}
                    >
                      <AssignmentIcon sx={{ fontSize: 40, color: 'text.secondary', mb: 1 }} />
                      <Typography color="text.secondary" align="center">
                        Bu durumda görev yok
                      </Typography>
                    </Box>
                  ) : (
                    tasks
                      .filter((task) => task.status === status)
                      .map((task) => {
                        // MongoDB ID ve frontend ID uyumsuzluğunu çöz
                        const taskId = task.id || task._id || '';
                        return (
                          <Todo
                            key={normalizeId(task)}
                            task={task}
                            onEdit={(updatedTask) => {
                              const updateId = normalizeId(updatedTask);
                              if (updateId) {
                                updateTask(updateId, updatedTask);
                              }
                            }}
                            onDelete={handleDeleteTask}
                            isOwner={isProjectOwner}
                          />
                        );
                      })
                  )}
                </Box>
              </Paper>
            </Box>
          ))}
        </Box>
      </DndContext>

      {/* Yeni Görev Dialogu */}
      <Dialog 
        open={openDialog} 
        onClose={() => setOpenDialog(false)}
        fullWidth
        maxWidth="sm"
      >
        <DialogTitle>
          {`${getStatusTitle(initialStatus)} Listesine Görev Ekle`}
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Görev Başlığı"
            fullWidth
            value={newTask.title}
            onChange={(e) =>
              setNewTask({ ...newTask, title: e.target.value })
            }
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Açıklama"
            fullWidth
            multiline
            rows={4}
            value={newTask.description}
            onChange={(e) =>
              setNewTask({ ...newTask, description: e.target.value })
            }
            sx={{ mb: 2 }}
          />
          
          {/* Öncelik Seçimi */}
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Öncelik
            </Typography>
            <Box sx={{ display: 'flex', gap: 1 }}>
              {['düşük', 'normal', 'yüksek'].map((priority) => (
                <Chip
                  key={`priority-${priority}`}
                  label={priority.charAt(0).toUpperCase() + priority.slice(1)}
                  onClick={() => setNewTask({ ...newTask, priority: priority as any })}
                  color={newTask.priority === priority ? 'primary' : 'default'}
                  variant={newTask.priority === priority ? 'filled' : 'outlined'}
                  sx={{ 
                    borderColor: getPriorityColor(priority),
                    backgroundColor: newTask.priority === priority ? getPriorityColor(priority) : 'transparent',
                    color: newTask.priority === priority ? 'white' : getPriorityColor(priority),
                  }}
                />
              ))}
            </Box>
          </Box>
          
          {/* Son Tarih Seçimi */}
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Son Tarih (Opsiyonel)
            </Typography>
            <TextField
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              onChange={(e) => {
                const selectedDate = e.target.value ? new Date(e.target.value) : null;
                setNewTask({ ...newTask, deadline: selectedDate });
              }}
              value={newTask.deadline ? newTask.deadline.toISOString().split('T')[0] : ''}
            />
          </Box>
          
          {/* Etiket Ekleme */}
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Etiketler (Virgülle ayırın)
            </Typography>
            <TextField
              fullWidth
              placeholder="örn: acil, toplantı, rapor"
              value={newTask.tags.join(', ')}
              onChange={(e) => {
                const tagsInput = e.target.value;
                const tagsArray = tagsInput.split(',').map(tag => tag.trim()).filter(tag => tag !== '');
                setNewTask({ ...newTask, tags: tagsArray });
              }}
            />
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mt: 1 }}>
              {newTask.tags.map((tag, index) => (
                <Chip 
                  key={index} 
                  label={tag} 
                  size="small"
                  onDelete={() => {
                    const newTags = [...newTask.tags];
                    newTags.splice(index, 1);
                    setNewTask({ ...newTask, tags: newTags });
                  }}
                />
              ))}
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenDialog(false)}>İptal</Button>
          <Button 
            onClick={handleAddTask} 
            variant="contained" 
            disabled={!newTask.title.trim()}
          >
            Oluştur
          </Button>
        </DialogActions>
      </Dialog>

      {/* Bildirim Snackbarı */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity}
          variant="filled"
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Container>
  );
};

export default ProjectDetailPage; 