import React, { createContext, useState, useContext, useEffect, useCallback, ReactNode } from 'react';
import axios from 'axios';
import { CircularProgress, Box } from '@mui/material';

// API URL
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

// Sağlık kontrolü durumunu ve zaman damgasını takip et
let lastConnectionCheck = 0;
let isConnectionCheckInProgress = false;
const connectionCheckCache = { isAvailable: true, timestamp: 0 };
const HEALTH_CHECK_INTERVAL = 300000; // 5 dakika (ms)

// API bağlantısını kontrol et (debounce mekanizması ile)
const isServerAvailable = async (): Promise<boolean> => {
  try {
    const now = Date.now();
    
    // Son kontrolden bu yana 5 dakika geçmediyse önbelleği kullan
    if (now - connectionCheckCache.timestamp < HEALTH_CHECK_INTERVAL) {
      return connectionCheckCache.isAvailable;
    }
    
    // Eğer kontrol zaten yapılıyorsa yeni istek gönderme
    if (isConnectionCheckInProgress) {
      return connectionCheckCache.isAvailable; // Son bilinen değeri kullan
    }
    
    isConnectionCheckInProgress = true;
    console.log('API bağlantısı kontrol ediliyor...');
    
    try {
      // İlk önce auth sağlık kontrolünü deneyelim
      const authResponse = await axios.get(`${API_URL}/auth/health`, { 
        timeout: 3000,
        validateStatus: (status) => true,
        headers: {
          'Cache-Control': 'no-cache, no-store, must-revalidate',
          'Pragma': 'no-cache',
          'Expires': '0'
        }
      });
      
      lastConnectionCheck = now;
      connectionCheckCache.timestamp = now;
      isConnectionCheckInProgress = false;
      
      if (authResponse.status === 200 || authResponse.status === 304) {
        console.log('Auth servis sağlık kontrolü başarılı:', authResponse.status);
        connectionCheckCache.isAvailable = true;
        return true;
      }
      
      console.log('Auth servis sağlık kontrolü yanıt kodu:', authResponse.status);
      
      if (authResponse.status < 500) {
        // 4xx hataları durumunda yine de sunucu çalışıyor demektir
        connectionCheckCache.isAvailable = true;
        return true;
      }
      
      // Şimdi de tasks API'si deneyelim
      try {
        const tasksResponse = await axios.get(`${API_URL}/tasks/health`, { 
          timeout: 3000,
          validateStatus: (status) => true,
          headers: {
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0'
          }
        });
        
        if (tasksResponse.status === 200 || tasksResponse.status === 304) {
          console.log('Tasks servis sağlık kontrolü başarılı:', tasksResponse.status);
          connectionCheckCache.isAvailable = true;
          return true;
        }
        
        if (tasksResponse.status < 500) {
          // 4xx hataları durumunda yine de sunucu çalışıyor demektir
          connectionCheckCache.isAvailable = true;
          return true;
        }
      } catch (tasksError: any) {
        console.warn('Tasks servis sağlık kontrolünde hata:', tasksError.message);
      }
      
      // Tüm kontroller başarısız olduysa bağlantı yok demektir
      connectionCheckCache.isAvailable = false;
      return false;
    } catch (error) {
      console.error('API bağlantı kontrolü hatası:', error);
      isConnectionCheckInProgress = false;
      connectionCheckCache.isAvailable = false;
      return false;
    }
  } catch (err) {
    console.error('API bağlantı kontrolü genel hatası:', err);
    isConnectionCheckInProgress = false;
    connectionCheckCache.isAvailable = false;
    return false;
  }
};

// Axios instance
const api = axios.create({
  baseURL: API_URL,
  headers: {
    'Content-Type': 'application/json'
  },
  timeout: 8000 // 10 saniyeden 8 saniyeye düşürülüyor
});

// Token ile yapılacak istekler için interceptor
api.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  error => Promise.reject(error)
);

// Hata durumunda otomatik çıkış için response interceptor
api.interceptors.response.use(
  response => response,
  error => {
    // 401 (Unauthorized) hatası durumunda otomatik çıkış
    if (error.response && error.response.status === 401) {
      console.warn('Yetkisiz erişim hatası tespit edildi, ancak otomatik çıkış yapılmayacak');
      console.log('Oturum durumu kullanıcı tarafından kontrol edilecek');
      
      // Token ve kullanıcı bilgisini silmeyelim, bunun yerine
      // checkTokenValidity fonksiyonunun kullanıcı tarafından çağrıldığında
      // doğru sonuç vermesini sağlayalım. Bu sayede sayfa yenilemesi veya
      // yönlendirme olmadan devam edilebilir.
    }
    return Promise.reject(error);
  }
);

// Kullanıcı ve context tipleri
interface User {
  id: string;
  name: string;
  surname: string;
  username: string;
  email: string;
}

interface AuthContextType {
  user: User | null;
  token: string | null;
  loading: boolean;
  error: string | null;
  login: (username: string, password: string) => Promise<boolean>;
  register: (userData: RegisterData) => Promise<boolean>;
  logout: () => void;
  setUser: (user: User | null) => void;
  clearError: () => void;
  checkTokenValidity: () => Promise<boolean>;
  isServerReachable: boolean;
}

interface RegisterData {
  name: string;
  surname: string;
  username: string;
  email: string;
  password: string;
  confirmPassword: string;
}

interface AuthProviderProps {
  children: ReactNode;
}

// Context oluştur
const AuthContext = createContext<AuthContextType | null>(null);

// Provider component
export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [token, setToken] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [initialized, setInitialized] = useState(false);
  const [isServerReachable, setIsServerReachable] = useState(true); // Başlangıçta bağlantı var kabul edilir
  const [isCheckingConnection, setIsCheckingConnection] = useState(false);
  const [lastTokenCheck, setLastTokenCheck] = useState<number>(0);
  
  // Çıkış yap - logout fonksiyonunu önce tanımlayalım
  const logout = useCallback(() => {
    console.log('Oturum kapatılıyor...');
    localStorage.removeItem('user');
    localStorage.removeItem('token');
    setUser(null);
    setToken(null);
  }, []);

  // API bağlantısını kontrol etme - 5 dakikada bir kontrol et
  useEffect(() => {
    const checkApiConnection = async () => {
      if (isCheckingConnection) return;
      
      try {
        setIsCheckingConnection(true);
        const isAvailable = await isServerAvailable();
        
        if (!isAvailable) {
          console.warn('API sunucusuna erişilemiyor');
        }
        
        // Sadece eğer değişiklik varsa state'i güncelle
        setIsServerReachable(prev => {
          if (prev !== isAvailable) {
            console.log('API bağlantı durumu değişti:', isAvailable ? 'bağlı' : 'bağlantı yok');
          }
          return isAvailable;
        });
      } catch (err) {
        console.error('API bağlantı kontrolü sırasında hata:', err);
      } finally {
        setIsCheckingConnection(false);
      }
    };
    
    // İlk açılışta ve her 5 dakikada bir kontrol et (60 saniyeden değiştirildi)
    checkApiConnection();
    const interval = setInterval(checkApiConnection, HEALTH_CHECK_INTERVAL);
    
    return () => clearInterval(interval);
  }, [isCheckingConnection]);

  // Token doğrulama işlemi
  const checkTokenValidity = useCallback(async (): Promise<boolean> => {
    try {
      const token = localStorage.getItem('token');
      
      if (!token) {
        console.error('Token bulunamadı, yönlendirme yapılıyor...');
        return false;
      }
      
      // Son kontrol zamanı kontrolü
      const now = Date.now();
      if (lastTokenCheck && (now - lastTokenCheck < 60000)) {  // 60 saniye
        console.log('Son token kontrolünden bu yana yeterli süre geçmedi, kontrol atlanıyor');
        return true; // Son kontrolden sonra kısa süre geçtiyse, token halen geçerli kabul edilir
      }
      
      console.log('Token doğrulaması yapılıyor...');
      
      // Token formatını kontrol et
      if (!isValidToken(token)) {
        console.error('Geçersiz token formatı, oturum kapatılıyor');
        logout();
        return false;
      }
      
      setLastTokenCheck(now);
      
      try {
        // Token kontrolü yaparken sunucu bağlantı durumunu güncellemeyelim
        // token kontrolünden bağımsız olarak yapılıyor
        console.log('Token doğrulaması yapılıyor...');
        
        // AbortController ekle ve zaman aşımı değerini azalt
        const controller = new AbortController();
        const timeoutId = setTimeout(() => {
          console.log('Token kontrolü zaman aşımına uğradı, iptal ediliyor...');
          controller.abort();
        }, 5000); // 5 saniye sonra iptal et
        
        try {
          // Headers'ı açıkça belirt
          const response = await axios.get(`${API_URL}/auth/me`, {
            headers: {
              'Authorization': `Bearer ${token}`,
              'Content-Type': 'application/json',
              'Cache-Control': 'no-cache, no-store, must-revalidate',
              'Pragma': 'no-cache',
              'Expires': '0'
            },
            timeout: 5000,
            signal: controller.signal
          });
          
          // Zamanlayıcıyı temizle
          clearTimeout(timeoutId);
          
          console.log('Token doğrulaması başarılı');
          const userData = response.data.user || response.data;
          
          // Kullanıcı bilgilerini güncelle
          if (userData) {
            setUser(userData);
            localStorage.setItem('user', JSON.stringify(userData));
            return true;
          }
          return false;
        } catch (requestError: any) {
          // Zamanlayıcıyı temizle
          clearTimeout(timeoutId);
          
          console.error('Token geçerli değil veya süresi dolmuş:', requestError.message);
          
          // Hata yanıtını kontrol et
          if (requestError.response) {
            console.log('Hata yanıtı:', requestError.response.status);
            
            // 401 Unauthorized hatası - oturum geçersiz, çıkış yap
            if (requestError.response.status === 401) {
              console.warn('401 Unauthorized hatası, oturum kapatılıyor');
              logout();
              return false;
            }
          } else if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
            console.warn('Token kontrolü zaman aşımına uğradı veya iptal edildi');
            // Zaman aşımında oturumu kapatma, sadece false döndür
            return false;
          }
          
          return false;
        }
      } catch (error: any) {
        console.error('Token doğrulama genel hatası:', error);
        // Oturum süresi dolmuşsa otomatik çıkış yap
        logout();
        return false;
      }
    } catch (error: any) {
      console.error('Token doğrulama genel hatası:', error);
      // Oturum süresi dolmuşsa otomatik çıkış yap
      logout();
      return false;
    }
  }, [user, lastTokenCheck, logout]);

  // Sayfa yüklendiğinde localStorage'dan kullanıcı bilgilerini al
  useEffect(() => {
    const loadUser = async () => {
      try {
        setLoading(true);
        
        const storedUser = localStorage.getItem('user');
        const storedToken = localStorage.getItem('token');
        
        if (storedUser && storedToken) {
          // Token formatını kontrol et
          if (!isValidToken(storedToken)) {
            console.error('Geçersiz token formatı');
            logout();
            return;
          }
          
          try {
            const userData = JSON.parse(storedUser);
            setUser(userData);
            setToken(storedToken);
            
            // Token geçerliliğini kontrol et - giriş sonrası yönlendirme hızı için burada kontrol etmeyebiliriz
            // ve doğrudan ilk verileri gösterip arka planda doğrulama yapabiliriz
            setLastTokenCheck(Date.now());
          } catch (parseErr) {
            console.error('Kullanıcı verisi JSON formatında değil:', parseErr);
            logout();
          }
        }
      } catch (err) {
        console.error('Kullanıcı bilgileri yüklenirken hata oluştu:', err);
        // Herhangi bir hata durumunda çıkış yap
        logout();
      } finally {
        setLoading(false);
        setInitialized(true);
      }
    };
    
    loadUser();
  }, []);

  // Token formatını kontrol eden yardımcı fonksiyon
  const isValidToken = (token: string): boolean => {
    if (!token) return false;
    
    // Token formatını kontrol et (örnek: JWT kontrolü)
    // JWT formatı: xxxxx.yyyyy.zzzzz
    const parts = token.split('.');
    return parts.length === 3;
  };

  // Hata mesajını temizle
  const clearError = () => setError(null);

  // Giriş yap
  const login = async (username: string, password: string): Promise<boolean> => {
    clearError();
    setLoading(true);
    
    try {
      // Gerçek API isteği
      const response = await api.post('/auth/login', {
        username,
        password
      });
      
      const userData = response.data;
      const userToStore = userData.user || userData; // API yanıtına bağlı olarak ayarla
      const tokenToStore = userData.token || userData.accessToken;
      
      if (!tokenToStore) {
        throw new Error('Token alınamadı');
      }
      
      // Token formatını kontrol et
      if (!isValidToken(tokenToStore)) {
        console.error('API yanıtında geçersiz token formatı');
        setError('Oturum açma sırasında bir sorun oluştu');
        return false;
      }
      
      localStorage.setItem('user', JSON.stringify(userToStore));
      localStorage.setItem('token', tokenToStore);
      
      setUser(userToStore);
      setToken(tokenToStore);
      setLastTokenCheck(Date.now());
      return true;
    } catch (err: any) {
      console.error('Login error:', err);
      
      if (err.code === 'ECONNABORTED' || err.message === 'Network Error') {
        setError('Sunucuya bağlanılamıyor. Lütfen internet bağlantınızı kontrol edin.');
      } else {
        setError(err.response?.data?.error || 'Giriş yapılırken bir hata oluştu');
      }
      
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Kayıt ol
  const register = async (userData: RegisterData): Promise<boolean> => {
    clearError();
    setLoading(true);
    
    try {
      // Gerçek API isteği
      const response = await api.post('/auth/register', userData);
      
      const responseData = response.data;
      const userToStore = responseData.user || responseData;
      const tokenToStore = responseData.token || responseData.accessToken;
      
      if (!tokenToStore) {
        throw new Error('Token alınamadı');
      }
      
      localStorage.setItem('user', JSON.stringify(userToStore));
      localStorage.setItem('token', tokenToStore);
      
      setUser(userToStore);
      setToken(tokenToStore);
      setLastTokenCheck(Date.now());
      return true;
    } catch (err: any) {
      console.error('Register error:', err);
      
      if (err.code === 'ECONNABORTED' || err.message === 'Network Error') {
        setError('Sunucuya bağlanılamıyor. Lütfen internet bağlantınızı kontrol edin.');
      } else {
        setError(err.response?.data?.error || 'Kayıt olurken bir hata oluştu');
      }
      
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Uygulama hala ilk yüklemesini tamamlamadıysa, yükleniyor göster
  if (!initialized) {
    return (
      <Box sx={{ 
        display: 'flex', 
        justifyContent: 'center', 
        alignItems: 'center', 
        height: '100vh' 
      }}>
        <CircularProgress size={40} />
      </Box>
    );
  }

  return (
    <AuthContext.Provider 
      value={{ 
        user, 
        token, 
        loading,
        error,
        login,
        register,
        logout,
        setUser,
        clearError,
        checkTokenValidity,
        isServerReachable
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

// Context kullanımı için hook
export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 