import React, { ReactNode, useEffect, useState } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { CssBaseline, AppBar, Toolbar, Typography, Button, Snackbar, Alert } from '@mui/material';

// Pages
import HomePage from './pages/HomePage';
import ProjectsPage from './pages/ProjectsPage';
import ProjectDetailPage from './pages/ProjectDetailPage';
import LoginPage from './pages/LoginPage';
import RegisterPage from './pages/RegisterPage';

// Context
import { AuthProvider, useAuth } from './context/AuthContext';
import { ProjectProvider } from './context/ProjectContext';
import { TaskProvider } from './context/TaskContext';
import { LoadingProvider } from './context/LoadingContext';
import GlobalLoader from './components/GlobalLoader';

// Tema tanımı
const theme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#1976d2',
    },
    secondary: {
      main: '#dc004e',
    },
  },
});

interface ProtectedRouteProps {
  children: ReactNode;
}

// Protected route component
const ProtectedRoute: React.FC<ProtectedRouteProps> = ({ children }) => {
  const { user, checkTokenValidity } = useAuth();
  const navigate = useNavigate();
  
  useEffect(() => {
    // Component mount olduğunda token geçerliliğini kontrol et
    const verifyToken = async () => {
      const isValid = await checkTokenValidity();
      if (!isValid) {
        navigate('/login');
      }
    };
    
    verifyToken();
  }, [checkTokenValidity, navigate]);
  
  if (!user) {
    return <Navigate to="/login" />;
  }
  
  return <>{children}</>;
};

// Basit NavigationBar bileşeni
const SimpleNavBar: React.FC = () => {
  const { user, logout } = useAuth();
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  return (
    <AppBar position="static">
      <Toolbar>
        <Typography variant="h6" sx={{ flexGrow: 1 }}>
          Görev Yöneticisi
        </Typography>
        
        {user ? (
          <>
            <Button color="inherit" onClick={() => navigate('/projects')}>
              Projelerim
            </Button>
            <Button color="inherit" onClick={handleLogout}>
              Çıkış Yap
            </Button>
          </>
        ) : (
          <>
            <Button color="inherit" onClick={() => navigate('/login')}>
              Giriş Yap
            </Button>
            <Button color="inherit" onClick={() => navigate('/register')}>
              Kayıt Ol
            </Button>
          </>
        )}
      </Toolbar>
    </AppBar>
  );
};

// TokenRefresher bileşeni - token geçerliliğini kontrol eder, ancak daha seyrek
const TokenRefresher: React.FC = () => {
  const { checkTokenValidity, user } = useAuth();
  const [lastCheck, setLastCheck] = useState<number>(0);
  
  useEffect(() => {
    // Sadece kullanıcı giriş yapmışsa token kontrolü yap
    if (!user) return;
    
    const now = Date.now();
    const timeSinceLastCheck = now - lastCheck;
    
    // İlk yükleme anında veya son kontrolden bu yana 2 dakika geçtiyse token kontrolü yap
    if (lastCheck === 0 || timeSinceLastCheck > 120000) {
      const checkToken = async () => {
        try {
          await checkTokenValidity();
          setLastCheck(Date.now());
        } catch (err) {
          console.error('Token kontrolü sırasında hata:', err);
        }
      };
      
      checkToken();
    }
    
    // 2 dakikada bir token kontrolü (30 saniyeden değiştirildi)
    const interval = setInterval(() => {
      checkTokenValidity()
        .then(() => setLastCheck(Date.now()))
        .catch(err => console.error('Zamanlanmış token kontrolünde hata:', err));
    }, 120000); // 2 dakika
    
    return () => clearInterval(interval);
  }, [checkTokenValidity, user, lastCheck]);
  
  return null;
};

// API bağlantı durumu kontrolü - iyileştirilmiş
const ApiConnectionStatus: React.FC = () => {
  const { isServerReachable } = useAuth();
  const [showMessage, setShowMessage] = useState(false);
  const [dismissCount, setDismissCount] = useState(0);
  
  // API bağlantı durumu değiştiğinde mesajı göster veya gizle
  useEffect(() => {
    if (!isServerReachable) {
      // Kullanıcı mesajı 3'ten fazla kapattıysa, artık rahatsız etmeyelim
      if (dismissCount < 3) {
        setShowMessage(true);
      }
    } else {
      // Bağlantı geri geldiğinde sayacı sıfırla
      setDismissCount(0);
      setShowMessage(false);
    }
  }, [isServerReachable, dismissCount]);
  
  const handleClose = () => {
    setShowMessage(false);
    setDismissCount(prev => prev + 1);
  };
  
  return (
    <Snackbar 
      open={!isServerReachable && showMessage} 
      autoHideDuration={10000}
      onClose={handleClose}
      anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
    >
      <Alert 
        severity="error" 
        sx={{ width: '100%' }}
        onClose={handleClose}
      >
        API sunucusuna bağlanılamıyor. Lütfen internet bağlantınızı ve sunucu durumunu kontrol edin.
      </Alert>
    </Snackbar>
  );
};

function AppContent() {
  return (
    <>
      <Router>
        <SimpleNavBar />
        <TokenRefresher />
        <ApiConnectionStatus />
        <GlobalLoader />
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/login" element={<LoginPage />} />
          <Route path="/register" element={<RegisterPage />} />
          <Route 
            path="/projects" 
            element={
              <ProtectedRoute>
                <ProjectsPage />
              </ProtectedRoute>
            } 
          />
          <Route 
            path="/projects/:projectId" 
            element={
              <ProtectedRoute>
                <ProjectDetailPage />
              </ProtectedRoute>
            } 
          />
          {/* Geçersiz route durumunda projeler sayfasına yönlendir */}
          <Route 
            path="*" 
            element={<Navigate to="/projects" />} 
          />
        </Routes>
      </Router>
    </>
  );
}

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <LoadingProvider>
        <AuthProvider>
          <ProjectProvider>
            <TaskProvider>
              <AppContent />
            </TaskProvider>
          </ProjectProvider>
        </AuthProvider>
      </LoadingProvider>
    </ThemeProvider>
  );
}

export default App;
