import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  IconButton, 
  Dialog, 
  DialogTitle, 
  DialogContent, 
  DialogActions, 
  TextField, 
  Button,
  Card,
  CardContent,
  CardActions,
  Chip,
  Avatar,
  Snackbar,
  Alert,
  CircularProgress
} from '@mui/material';
import { useDraggable } from '@dnd-kit/core';
import { normalizeId } from '../utils/dataUtils';
import { Task } from '../types';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import PersonIcon from '@mui/icons-material/Person';

interface TodoProps {
  task: Task;
  onEdit: (updatedTask: Task) => void;
  onDelete: (taskId: string) => Promise<boolean>;
  isOwner?: boolean;
}

// Öncelik renkleri
const getPriorityColor = (priority: string | undefined) => {
  switch (priority) {
    case 'yüksek':
      return '#d32f2f'; // Koyu kırmızı
    case 'normal':
      return '#1976d2'; // Mavi
    case 'düşük':
      return '#388e3c'; // Yeşil
    default:
      return '#757575'; // Gri
  }
};

// Öncelik adı
const getPriorityLabel = (priority: string | undefined) => {
  if (!priority) return null;
  return priority.charAt(0).toUpperCase() + priority.slice(1);
};

const Todo: React.FC<TodoProps> = ({ task, onEdit, onDelete, isOwner = false }) => {
  const [openEditDialog, setOpenEditDialog] = useState(false);
  const [editedTask, setEditedTask] = useState<Task>(task);
  const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);
  const [snackbar, setSnackbar] = useState<{
    open: boolean;
    message: string;
    severity: 'success' | 'error' | 'info' | 'warning';
  }>({
    open: false,
    message: '',
    severity: 'info'
  });
  
  // İşlem durumunu izleyen state'ler
  const [isEditLoading, setIsEditLoading] = useState(false);
  const [isDeleteLoading, setIsDeleteLoading] = useState(false);
  
  // MongoDB'den gelen görevlerden ID alanını normalleştir
  const taskId = normalizeId(task);
  
  const { attributes, listeners, setNodeRef, transform } = useDraggable({
    id: taskId,
    data: { task }
  });

  // Düzenleme ve silme fonksiyonlarını basitleştirelim
  const handleEditClick = () => {
    // Eğer zaten yükleme devam ediyorsa, çık
    if (isEditLoading) return;
    
    // Görsel geribildirim için yüklenme durumunu aç
    setIsEditLoading(true);
    
    // Düzenleme için task verilerini hazırla
    setEditedTask({ ...task });
    
    // Dialog'u aç (kısa bir gecikmeyle işlem başladığı belli olsun)
    setTimeout(() => {
      setOpenEditDialog(true);
      setIsEditLoading(false);
    }, 100);
  };

  const handleDeleteClick = () => {
    // Eğer zaten yükleme devam ediyorsa, çık
    if (isDeleteLoading) return;
    
    // Görsel geribildirim için yüklenme durumunu aç
    setIsDeleteLoading(true);
    
    // Dialog'u aç (kısa bir gecikmeyle işlem başladığı belli olsun)
    setTimeout(() => {
      setOpenDeleteDialog(true);
      setIsDeleteLoading(false);
    }, 100);
  };

  const handleSaveEdit = () => {
    // Anında görsel geri bildirim için snackbarı göster
    setSnackbar({
      open: true,
      message: 'Değişiklikler kaydediliyor...',
      severity: 'info'
    });
    
    // UI'ı kilitleme
    setIsEditLoading(true);
    
    // Dialog'u kapat
    setOpenEditDialog(false);
    
    // Kısa bir bekleme ile görevi güncelle
    setTimeout(() => {
      // Görevi güncelle
      onEdit(editedTask);
      
      // İşlemi tamamla
      setIsEditLoading(false);
    }, 100);
  };

  const handleConfirmDelete = async () => {
    // Dialogu kapat
    setOpenDeleteDialog(false);
    
    // Görev ID kontrolü
    if (!taskId) {
      console.error('Görev ID bulunamadı');
      setSnackbar({
        open: true,
        message: 'Görev ID bulunamadı!',
        severity: 'error'
      });
      return;
    }
    
    // UI geribildirim için durumu güncelle
    setIsDeleteLoading(true);
    setIsDeleting(true);
    
    // Görsel geribildirim için snackbarı göster
    setSnackbar({
      open: true,
      message: 'Görev siliniyor...',
      severity: 'info'
    });
    
    console.log('Todo - Silme işlemi başlatılıyor, ID:', taskId);
    
    try {
      console.log('Todo - onDelete fonksiyonu çağrılıyor...');
      const result = await onDelete(taskId);
      console.log('Todo - Silme işlemi sonucu:', result);
      
      if (result) {
        // Başarılı silme
        console.log('Todo - Görev başarıyla silindi');
        setSnackbar({
          open: true,
          message: 'Görev başarıyla silindi!',
          severity: 'success'
        });
      } else {
        // Başarısız silme
        console.error('Todo - Görev silinemedi');
        setIsDeleting(false);
        setSnackbar({
          open: true,
          message: 'Görev silinirken bir hata oluştu!',
          severity: 'error'
        });
      }
    } catch (error) {
      console.error('Todo - Silme işlemi sırasında hata:', error);
      setIsDeleting(false);
      setSnackbar({
        open: true,
        message: `Silme hatası: ${error instanceof Error ? error.message : 'Bilinmeyen hata'}`,
        severity: 'error'
      });
    } finally {
      // İşlem tamamlandığında loading durumunu kapat
      setIsDeleteLoading(false);
      console.log('Todo - Silme işlemi tamamlandı (başarılı veya başarısız)');
    }
  };

  // Snackbar kapatma fonksiyonu
  const handleCloseSnackbar = () => {
    setSnackbar(prev => ({ ...prev, open: false }));
  };

  const style = transform ? {
    transform: `translate3d(${transform.x}px, ${transform.y}px, 0)`,
    zIndex: 999
  } : undefined;

  // Görevin durumuna göre renk belirleme
  const getStatusColor = () => {
    switch (task.status) {
      case 'yapiliyor':
        return '#f44336';
      case 'beklemede':
        return '#ffeb3b';
      case 'tamamlandi':
        return '#4caf50';
      default:
        return '#e0e0e0';
    }
  };

  return (
    <>
      <Card 
        ref={setNodeRef} 
        style={style} 
        {...(isOwner ? listeners : {})}
        {...(isOwner ? attributes : {})}
        sx={{ 
          mb: 2, 
          borderLeft: `4px solid ${getStatusColor()}`,
          cursor: isOwner ? 'grab' : 'default',
          boxShadow: '0 2px 4px rgba(0,0,0,0.08)',
          position: 'relative',
          opacity: isDeleting ? 0.5 : 1,
          transition: 'all 0.2s ease',
          borderRadius: '8px',
          overflow: 'hidden',
          '&:hover': {
            boxShadow: '0 3px 10px rgba(0,0,0,0.12)',
          }
        }}
      >
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
            <Typography variant="h6" component="div" gutterBottom>
              {task.title}
            </Typography>
            
            {/* Düzenle ve Sil butonları */}
            {isOwner && (
              <Box sx={{ 
                display: 'flex', 
                position: 'absolute', 
                top: 8, 
                right: 8, 
                gap: 1,
                zIndex: 100
              }}>
                <IconButton
                  onClick={handleEditClick}
                  aria-label="görevi düzenle"
                  title="Görevi Düzenle"
                  size="small"
                  disabled={isEditLoading}
                  sx={{
                    backgroundColor: 'white',
                    color: '#2196f3', // Mavi
                    width: 40,
                    height: 40,
                    borderRadius: '50%',
                    boxShadow: '0 1px 3px rgba(0,0,0,0.12)',
                    '&:hover': {
                      backgroundColor: '#f5f5f5',
                      transform: 'translateY(-2px)',
                      boxShadow: '0 3px 5px rgba(0,0,0,0.2)',
                    },
                    transition: 'all 0.2s ease'
                  }}
                >
                  {isEditLoading ? (
                    <CircularProgress size={18} color="inherit" />
                  ) : (
                    <EditIcon fontSize="small" />
                  )}
                </IconButton>
                
                <IconButton
                  onClick={handleDeleteClick}
                  aria-label="görevi sil"
                  title="Görevi Sil"
                  size="small"
                  disabled={isDeleteLoading}
                  sx={{
                    backgroundColor: 'white',
                    color: '#f44336', // Kırmızı
                    width: 40,
                    height: 40,
                    borderRadius: '50%',
                    boxShadow: '0 1px 3px rgba(0,0,0,0.12)',
                    '&:hover': {
                      backgroundColor: '#f5f5f5',
                      transform: 'translateY(-2px)',
                      boxShadow: '0 3px 5px rgba(0,0,0,0.2)',
                    },
                    transition: 'all 0.2s ease'
                  }}
                >
                  {isDeleteLoading ? (
                    <CircularProgress size={18} color="inherit" />
                  ) : (
                    <DeleteIcon fontSize="small" />
                  )}
                </IconButton>
              </Box>
            )}
          </Box>
          
          <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
            {task.description}
          </Typography>
          
          {/* Öncelik varsa göster */}
          {task.priority && (
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1, gap: 1 }}>
              <Chip 
                label={getPriorityLabel(task.priority)} 
                size="small"
                sx={{ 
                  bgcolor: getPriorityColor(task.priority),
                  color: 'white',
                }}
              />
            </Box>
          )}
          
          {/* Etiketler varsa göster */}
          {task.tags && task.tags.length > 0 && (
            <Box sx={{ display: 'flex', flexWrap: 'wrap', mb: 1, gap: 0.5 }}>
              {task.tags.map((tag, index) => (
                <Chip 
                  key={`tag-${task.id}-${index}`} 
                  label={tag} 
                  size="small" 
                  variant="outlined"
                />
              ))}
            </Box>
          )}
          
          {task.deadline && (
            <Box sx={{ display: 'flex', alignItems: 'center', mb: 1 }}>
              <AccessTimeIcon fontSize="small" sx={{ mr: 1, color: 'text.secondary' }} />
              <Typography variant="body2" color="text.secondary">
                {new Date(task.deadline).toLocaleDateString('tr-TR')}
              </Typography>
            </Box>
          )}
          
          {task.assignedTo && task.assignedTo.length > 0 && (
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <PersonIcon fontSize="small" sx={{ mr: 1, color: 'text.secondary' }} />
              <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
                {task.assignedTo.map((assigned, index) => (
                  <Chip 
                    key={`assigned-${task.id}-${index}`} 
                    size="small" 
                    label={assigned.user.name || 'Atamalı'}
                    avatar={<Avatar>{(assigned.user.name || '?')[0]}</Avatar>}
                  />
                ))}
              </Box>
            </Box>
          )}
        </CardContent>
        
        <CardActions>
          <Typography variant="caption" color="text.secondary">
            {new Date(task.updatedAt).toLocaleDateString('tr-TR')}
          </Typography>
        </CardActions>
      </Card>

      {/* Düzenleme Dialogu */}
      <Dialog open={openEditDialog} onClose={() => setOpenEditDialog(false)}>
        <DialogTitle>Görevi Düzenle</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Görev Başlığı"
            fullWidth
            value={editedTask.title}
            onChange={(e) => setEditedTask({ ...editedTask, title: e.target.value })}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Açıklama"
            fullWidth
            multiline
            rows={4}
            value={editedTask.description}
            onChange={(e) => setEditedTask({ ...editedTask, description: e.target.value })}
            sx={{ mb: 2 }}
          />
          
          {/* Öncelik Seçimi */}
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Öncelik
            </Typography>
            <Box sx={{ display: 'flex', gap: 1 }}>
              {['düşük', 'normal', 'yüksek'].map((priority) => (
                <Chip
                  key={priority}
                  label={priority.charAt(0).toUpperCase() + priority.slice(1)}
                  onClick={() => setEditedTask({ ...editedTask, priority: priority as any })}
                  color={editedTask.priority === priority ? 'primary' : 'default'}
                  variant={editedTask.priority === priority ? 'filled' : 'outlined'}
                  sx={{ 
                    borderColor: getPriorityColor(priority),
                    backgroundColor: editedTask.priority === priority ? getPriorityColor(priority) : 'transparent',
                    color: editedTask.priority === priority ? 'white' : getPriorityColor(priority),
                  }}
                />
              ))}
            </Box>
          </Box>
          
          {/* Son Tarih Seçimi */}
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Son Tarih (Opsiyonel)
            </Typography>
            <TextField
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              onChange={(e) => {
                const selectedDate = e.target.value ? new Date(e.target.value) : undefined;
                setEditedTask({ ...editedTask, deadline: selectedDate });
              }}
              value={editedTask.deadline ? new Date(editedTask.deadline).toISOString().split('T')[0] : ''}
            />
          </Box>
          
          {/* Etiket Ekleme */}
          <Box sx={{ mb: 2 }}>
            <Typography variant="subtitle2" gutterBottom>
              Etiketler (Virgülle ayırın)
            </Typography>
            <TextField
              fullWidth
              placeholder="örn: acil, toplantı, rapor"
              value={editedTask.tags ? editedTask.tags.join(', ') : ''}
              onChange={(e) => {
                const tagsInput = e.target.value;
                const tagsArray = tagsInput.split(',').map(tag => tag.trim()).filter(tag => tag !== '');
                setEditedTask({ ...editedTask, tags: tagsArray });
              }}
            />
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5, mt: 1 }}>
              {editedTask.tags && editedTask.tags.map((tag, index) => (
                <Chip 
                  key={`edit-tag-${editedTask.id}-${index}`} 
                  label={tag} 
                  size="small"
                  onDelete={() => {
                    if (!editedTask.tags) return;
                    const newTags = [...editedTask.tags];
                    newTags.splice(index, 1);
                    setEditedTask({ ...editedTask, tags: newTags });
                  }}
                />
              ))}
            </Box>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpenEditDialog(false)}>İptal</Button>
          <Button onClick={handleSaveEdit} variant="contained">Kaydet</Button>
        </DialogActions>
      </Dialog>

      {/* Silme Onay Dialogu */}
      <Dialog 
        open={openDeleteDialog} 
        onClose={() => setOpenDeleteDialog(false)}
        PaperProps={{
          sx: {
            borderRadius: '8px',
            minWidth: {
              xs: '90%',
              sm: '400px'
            }
          }
        }}
        // Arka planı karartarak daha belirgin hale getir ve tıklamaları engelle
        BackdropProps={{
          sx: {
            backgroundColor: 'rgba(0, 0, 0, 0.7)'
          }
        }}
        // Kapanmayı öncelikli hale getir - doğrudan ESC veya dışarı tıklama ile kapatılabilir
        disableEscapeKeyDown={false}
        keepMounted
      >
        <DialogTitle sx={{ 
          bgcolor: '#ffebee', 
          color: '#d32f2f',
          pb: 1,
          display: 'flex',
          alignItems: 'center'
        }}>
          <DeleteIcon sx={{ mr: 1 }} /> Görevi Sil
        </DialogTitle>
        <DialogContent sx={{ pt: 3, pb: 2 }}>
          <Typography variant="body1">
            <Box component="span" sx={{ fontWeight: 'bold' }}>"{task.title}"</Box> görevini silmek istediğinizden emin misiniz?
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
            Bu işlem geri alınamaz.
          </Typography>
        </DialogContent>
        <DialogActions sx={{ px: 3, pb: 2 }}>
          <Button 
            onClick={() => setOpenDeleteDialog(false)}
            variant="outlined"
            size="large"
            sx={{
              py: 1,
              minWidth: '100px',
              fontSize: '1rem'
            }}
          >
            İptal
          </Button>
          <Button 
            onClick={handleConfirmDelete} 
            color="error" 
            variant="contained"
            startIcon={<DeleteIcon />}
            size="large"
            sx={{ 
              ml: 1,
              py: 1,
              minWidth: '100px',
              fontSize: '1rem'
            }}
          >
            Sil
          </Button>
        </DialogActions>
      </Dialog>

      {/* Snackbar */}
      <Snackbar 
        open={snackbar.open} 
        autoHideDuration={6000} 
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={handleCloseSnackbar} 
          severity={snackbar.severity} 
          variant="filled"
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default Todo; 