import React, { createContext, useState, useContext, useCallback, ReactNode, useRef } from 'react';
import { Task } from '../types';
import axios from 'axios';
import { normalizeMongoData, normalizeId } from '../utils/dataUtils';
import { taskAPI } from '../services/api';

// API URL
const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api';

interface TasksState {
  tasks: Task[];
  loading: boolean;
  error: string | null;
}

interface TaskContextProps {
  tasks: Task[];
  loading: boolean;
  error: string | null;
  loadTasks: (projectId: string) => Promise<Task[]>;
  addTask: (projectId: string, taskData: Partial<Task>) => Promise<Task | null>;
  updateTask: (taskId: string, taskData: Partial<Task>) => Promise<Task | null>;
  deleteTask: (taskId: string) => Promise<boolean>;
  moveTask: (taskId: string, newStatus: Task['status']) => Promise<Task | null>;
  clearTasks: () => void;
}

const TaskContext = createContext<TaskContextProps | undefined>(undefined);

// Görev istek önbelleği için tanımlama
interface TaskCache {
  [projectId: string]: {
    tasks: Task[];
    timestamp: number;
  };
}

export const TaskProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, setState] = useState<TasksState>({
    tasks: [],
    loading: false,
    error: null,
  });

  // Önbellek için bir referans oluştur (re-render'lar arasında korunsun)
  const taskCacheRef = useRef<TaskCache>({});
  // İstek izleme için referans (aynı anda aynı istek için)
  const pendingRequestsRef = useRef<{[key: string]: boolean}>({});
  // En son istek zaman damgası
  const lastRequestTimesRef = useRef<{[key: string]: number}>({});

  // Önbellek için maksimum yaş (10 dakika)
  const CACHE_MAX_AGE = 10 * 60 * 1000; // 5 dakikadan 10 dakikaya çıkarıyoruz
  
  // Tekrar istek yapma aralığı (5 saniye)
  const DEBOUNCE_TIME = 5000; // 3 saniyeden 5 saniyeye çıkarıyoruz

  // Projeye ait görevleri yükle
  const loadTasks = useCallback(async (projectId: string): Promise<Task[]> => {
    if (!projectId) {
      console.error('loadTasks: projectId eksik veya boş');
      return [];
    }
    
    try {
      // İstek debouncing - aynı projeye belirli saniye içinde birden fazla istek göndermeyi engelle
      const now = Date.now();
      const lastRequestTime = lastRequestTimesRef.current[projectId] || 0;
      if (now - lastRequestTime < DEBOUNCE_TIME) {
        console.log(`Son ${DEBOUNCE_TIME/1000} saniye içinde ${projectId} için tasks isteği yapıldı, tekrar istek yapılmıyor`);
        
        // Eğer önbellekte veri varsa onu döndür
        if (taskCacheRef.current[projectId] && taskCacheRef.current[projectId].tasks.length > 0) {
          return taskCacheRef.current[projectId].tasks;
        }
        
        // Eğer hala istek devam ediyorsa bekle
        if (pendingRequestsRef.current[projectId]) {
          console.log(`${projectId} için hala devam eden istek var, bekleyin...`);
          setState(prev => ({ ...prev, loading: true }));
          return state.tasks; // Mevcut görevleri döndür
        }
      }
      
      // Son istek zamanını güncelle
      lastRequestTimesRef.current[projectId] = now;
      
      // Önbellek kontrolü
      const cachedData = taskCacheRef.current[projectId];
      if (cachedData && now - cachedData.timestamp < CACHE_MAX_AGE) {
        console.log(`Önbellekten ${projectId} için görevler yükleniyor`);
        return cachedData.tasks;
      }
      
      // Eğer aynı proje için istek zaten devam ediyorsa
      if (pendingRequestsRef.current[projectId]) {
        console.log(`${projectId} için istek zaten devam ediyor`);
        return state.tasks; // Mevcut görevleri döndür
      }
      
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // İsteği başlat
      pendingRequestsRef.current[projectId] = true;
      
      // Token doğrudan alınıyor
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('Token bulunamadı, görevler yüklenemiyor');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilgileriniz eksik veya süresi dolmuş',
          loading: false,
        }));
        pendingRequestsRef.current[projectId] = false;
        return [];
      }
      
      // Token format kontrolü (basit)
      if (!token.match(/^[A-Za-z0-9-_]+\.[A-Za-z0-9-_]+\.[A-Za-z0-9-_.+/=]*$/)) {
        console.error('Token formatı geçersiz:', token.substring(0, 10) + '...');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilginiz geçersiz, lütfen tekrar giriş yapın',
          loading: false,
        }));
        pendingRequestsRef.current[projectId] = false;
        return [];
      }
      
      console.log(`Görevler yükleniyor, proje ID: ${projectId}`);
      
      // AbortController ekle
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('Görev yükleme işlemi zaman aşımına uğradı, iptal ediliyor...');
        controller.abort();
      }, 12000);
      
      try {
        // Token açıkça belirtiliyor
        const response = await axios.get(`${API_URL}/tasks/project/${projectId}`, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0'
          },
          timeout: 12000,
          signal: controller.signal
        });
        
        // Zamanlayıcıyı temizle
        clearTimeout(timeoutId);
        
        const loadedTasks = response.data;
        console.log('Yüklenen görevler:', loadedTasks);
        
        // MongoDB'den gelen verileri normalize et
        const normalizedTasks = normalizeMongoData<Task[]>(loadedTasks);
        console.log('Normalize edilmiş görevler:', normalizedTasks);
        
        // Görevleri önbelleğe al
        taskCacheRef.current[projectId] = {
          tasks: normalizedTasks,
          timestamp: Date.now()
        };
        
        setState(prev => ({
          ...prev,
          tasks: normalizedTasks,
          loading: false,
        }));
        
        // İsteği kapat
        pendingRequestsRef.current[projectId] = false;
        
        return normalizedTasks;
      } catch (requestError: any) {
        // Zamanlayıcıyı temizle
        clearTimeout(timeoutId);
        
        console.error('Görevler yüklenirken istek hatası:', requestError);
        let errorMessage = 'Görevler yüklenemedi';
        
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          errorMessage = 'Sunucu yanıt vermiyor, lütfen daha sonra tekrar deneyin';
        } else if (requestError.response) {
          // HTTP hata yanıtı var
          const status = requestError.response.status;
          
          if (status === 401 || status === 403) {
            errorMessage = 'Oturum bilgileriniz geçersiz veya yetkisiz, lütfen tekrar giriş yapın';
            // Token sorunlu olabilir, diğer işlemlerde de hata almamak için localStorage'dan temizleyelim
            if (status === 401) {
              console.warn('Yetkilendirme başarısız, tokeni temizliyorum');
              localStorage.removeItem('token');
            }
          } else if (status === 404) {
            errorMessage = 'Proje bulunamadı veya silinmiş olabilir';
          } else if (requestError.response.data?.error) {
            errorMessage = requestError.response.data.error;
          } else {
            errorMessage = `Sunucu hatası: ${status}`;
          }
        } else if (requestError.message) {
          errorMessage = requestError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        // İsteği kapat
        pendingRequestsRef.current[projectId] = false;
        
        return [];
      }
    } catch (error: any) {
      console.error('Görevler yüklenirken genel hata:', error);
      let errorMessage = 'Görevler yüklenemedi';
      
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = 'Sunucu yanıt vermiyor, lütfen daha sonra tekrar deneyin';
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      // İsteği kapat
      pendingRequestsRef.current[projectId] = false;
      
      return [];
    }
  }, [state.tasks]);

  // Yeni görev eklendiğinde önbelleği temizleyen yardımcı fonksiyon
  const invalidateTaskCache = useCallback((projectId: string) => {
    if (projectId && taskCacheRef.current[projectId]) {
      console.log(`${projectId} için görev önbelleği temizleniyor`);
      delete taskCacheRef.current[projectId];
    }
  }, []);

  // Yeni görev oluştur
  const addTask = useCallback(async (projectId: string, taskData: Partial<Task>): Promise<Task | null> => {
    if (!projectId) {
      console.error('addTask: projectId eksik veya boş');
      return null;
    }
    
    try {
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // Token doğrudan alınıyor
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('Token bulunamadı, görev eklenemiyor');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilgileriniz eksik veya süresi dolmuş',
          loading: false,
        }));
        return null;
      }
      
      console.log(`Görev ekleniyor, proje ID: ${projectId}, görev verileri:`, taskData);
      
      // Default status değeri ayarlayalım
      const taskWithDefaults = {
        ...taskData,
        status: taskData.status || 'yapiliyor'
      };
      
      // Hızlı yanıt için AbortController ekle
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('Görev ekleme işlemi zaman aşımına uğradı, iptal ediliyor...');
        controller.abort();
      }, 15000); // 15 saniye sonra iptal et
      
      // İstek yapılandırması
      const config = {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
          'Cache-Control': 'no-cache, no-store, must-revalidate',
          'Pragma': 'no-cache',
          'Expires': '0'
        },
        timeout: 15000,
        signal: controller.signal
      };
      
      try {
        // Token açıkça belirtiliyor
        const response = await axios.post(`${API_URL}/tasks/project/${projectId}`, taskWithDefaults, config);
        
        // AbortController için zamanlayıcıyı temizle
        clearTimeout(timeoutId);
        
        const newTask = response.data;
        console.log('Yeni görev oluşturuldu (ham veri):', newTask);
        
        // MongoDB'den gelen veriyi normalize et
        const normalizedTask = normalizeMongoData<Task>(newTask);
        console.log('Yeni görev normalize edildi:', normalizedTask);
        
        // Görev eklendiğinde proje önbelleğini temizle
        invalidateTaskCache(projectId);
        
        setState(prev => ({
          ...prev,
          tasks: [...prev.tasks, normalizedTask],
          loading: false,
        }));
        
        return normalizedTask;
      } catch (requestError: any) {
        // AbortController için zamanlayıcıyı temizle
        clearTimeout(timeoutId);
        
        console.error('Görev oluşturma isteği hatası:', requestError);
        let errorMessage = 'Görev oluşturulamadı';
        
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          errorMessage = 'Sunucu yanıt vermiyor, lütfen daha sonra tekrar deneyin';
        } else if (requestError.response?.data?.error) {
          errorMessage = requestError.response.data.error;
          console.error('Sunucu hata mesajı:', errorMessage);
        } else if (requestError.message) {
          errorMessage = requestError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        return null;
      }
    } catch (error: any) {
      console.error('Görev oluşturma genel hatası:', error);
      let errorMessage = 'Görev oluşturulamadı: Beklenmeyen bir hata oluştu';
      
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
        console.error('Sunucu hata mesajı:', errorMessage);
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = 'Sunucu yanıt vermiyor, lütfen daha sonra tekrar deneyin';
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      return null;
    }
  }, [invalidateTaskCache]);

  // Görevi güncelle
  const updateTask = useCallback(async (taskId: string, taskData: Partial<Task>): Promise<Task | null> => {
    if (!taskId) {
      console.error('updateTask: taskId eksik veya boş');
      return null;
    }
    
    try {
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // Token doğrudan alınıyor
      const token = localStorage.getItem('token');
      if (!token) {
        console.error('Token bulunamadı, görev güncellenemiyor');
        setState(prev => ({
          ...prev,
          error: 'Oturum bilgileriniz eksik veya süresi dolmuş',
          loading: false,
        }));
        return null;
      }
      
      console.log(`Görev güncelleniyor, ID: ${taskId}, güncellenen veriler:`, taskData);
      
      // Hızlı yanıt için AbortController ekle
      const controller = new AbortController();
      const timeoutId = setTimeout(() => {
        console.log('Görev güncelleme işlemi zaman aşımına uğradı, iptal ediliyor...');
        controller.abort();
      }, 10000); // 10 saniye sonra iptal et
      
      try {
        // Token açıkça belirtiliyor
        const response = await axios.put(`${API_URL}/tasks/${taskId}`, taskData, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
            'Cache-Control': 'no-cache, no-store, must-revalidate',
            'Pragma': 'no-cache',
            'Expires': '0'
          },
          timeout: 10000,
          signal: controller.signal
        });
        
        // AbortController için zamanlayıcıyı temizle
        clearTimeout(timeoutId);
        
        const updatedTask = response.data;
        console.log('Görev güncellendi (ham veri):', updatedTask);
        
        // MongoDB'den gelen veriyi normalize et
        const normalizedTask = normalizeMongoData<Task>(updatedTask);
        console.log('Görev normalize edildi:', normalizedTask);
        
        // Önbelleği güncelle (ilgili projeyi bul)
        const affectedProjectId = normalizedTask.project as string;
        if (affectedProjectId) {
          invalidateTaskCache(affectedProjectId);
        }
        
        // Güncellenen görevi state'te de güncelle - hem id hem de _id ile eşleştir
        setState(prev => ({
          ...prev,
          tasks: prev.tasks.map(task => 
            normalizeId(task) === normalizeId(taskId) ? { ...task, ...normalizedTask } : task
          ),
          loading: false,
        }));
        
        return normalizedTask;
      } catch (requestError: any) {
        // AbortController için zamanlayıcıyı temizle
        clearTimeout(timeoutId);
        
        console.error('Görev güncelleme isteği hatası:', requestError);
        let errorMessage = 'Görev güncellenemedi';
        
        if (requestError.name === 'AbortError' || requestError.code === 'ECONNABORTED') {
          errorMessage = 'Sunucu yanıt vermiyor, lütfen daha sonra tekrar deneyin';
        } else if (requestError.response?.data?.error) {
          errorMessage = requestError.response.data.error;
          console.error('Sunucu hata mesajı:', errorMessage);
        } else if (requestError.message) {
          errorMessage = requestError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        return null;
      }
    } catch (error: any) {
      console.error('Görev güncelleme genel hatası:', error);
      let errorMessage = 'Görev güncellenemedi: Beklenmeyen bir hata oluştu';
      
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.code === 'ECONNABORTED') {
        errorMessage = 'Sunucu yanıt vermiyor, lütfen daha sonra tekrar deneyin';
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      return null;
    }
  }, [invalidateTaskCache]);

  // Görevi sil
  const deleteTask = useCallback(async (taskId: string): Promise<boolean> => {
    if (!taskId) {
      console.error('TaskContext - deleteTask: taskId eksik veya boş');
      return false;
    }
    
    try {
      console.log('TaskContext - Görev silme işlemi başlatılıyor, ID:', taskId);
      setState(prev => ({ ...prev, loading: true, error: null }));
      
      // Görev silme işlemi öncesinde, etkilenecek proje ID'sini bulmak için
      // ilgili görevi bul (önbellek güncellemesi için kullanılacak)
      const taskToDelete = state.tasks.find(task => 
        normalizeId(task) === normalizeId(taskId)
      );
      
      const affectedProjectId = taskToDelete?.project as string;
      console.log('TaskContext - Etkilenecek proje ID:', affectedProjectId);
      
      try {
        // taskAPI üzerinden silme işlemini gerçekleştir
        console.log('TaskContext - taskAPI.deleteTask çağrılıyor...');
        const success = await taskAPI.deleteTask(taskId);
        console.log('TaskContext - API silme işlemi sonucu:', success);
        
        if (success) {
          // Silinen görevi listeden kaldır
          setState(prev => {
            console.log('TaskContext - State güncelleniyor, silinen görev ID:', taskId);
            
            // Hem id hem de _id ile karşılaştırarak filtrele
            const updatedTasks = prev.tasks.filter(task => 
              normalizeId(task) !== normalizeId(taskId)
            );
            
            console.log('TaskContext - Filtreleme sonrası kalan görev sayısı:', updatedTasks.length);
            
            return {
              ...prev,
              tasks: updatedTasks,
              loading: false,
              error: null
            };
          });
          
          // Etkilenen proje varsa önbelleği temizle
          if (affectedProjectId) {
            console.log('TaskContext - Etkilenen proje önbelleği temizleniyor:', affectedProjectId);
            invalidateTaskCache(affectedProjectId);
          }
          
          return true;
        } else {
          setState(prev => ({
            ...prev,
            error: 'Görev silme işlemi başarısız oldu',
            loading: false,
          }));
          return false;
        }
      } catch (apiError: any) {
        console.error('TaskContext - API silme işlemi hatası:', apiError);
        
        let errorMessage = 'Görev silinemedi';
        if (apiError.response?.data?.error) {
          errorMessage = apiError.response.data.error;
        } else if (apiError.message) {
          errorMessage = apiError.message;
        }
        
        setState(prev => ({
          ...prev,
          error: errorMessage,
          loading: false,
        }));
        
        throw apiError;
      }
    } catch (error: any) {
      console.error('TaskContext - Görev silme genel hatası:', error);
      
      let errorMessage = 'Görev silinemedi';
      if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setState(prev => ({
        ...prev,
        error: errorMessage,
        loading: false,
      }));
      
      return false;
    }
  }, [state.tasks, invalidateTaskCache]);

  // Görevi taşı (durumunu değiştir)
  const moveTask = useCallback(async (taskId: string, newStatus: Task['status']): Promise<Task | null> => {
    console.log(`Görev taşınıyor: ${taskId} -> ${newStatus}`);
    return updateTask(taskId, { status: newStatus });
  }, [updateTask]);

  // Görevleri temizle
  const clearTasks = useCallback(() => {
    console.log('Görevler temizleniyor');
    setState(prev => ({
      ...prev,
      tasks: [],
      error: null,
    }));
  }, []);

  const value = {
    tasks: state.tasks,
    loading: state.loading,
    error: state.error,
    loadTasks,
    addTask,
    updateTask,
    deleteTask,
    moveTask,
    clearTasks,
  };

  return <TaskContext.Provider value={value}>{children}</TaskContext.Provider>;
};

export const useTaskContext = () => {
  const context = useContext(TaskContext);
  if (context === undefined) {
    throw new Error('useTaskContext must be used within a TaskProvider');
  }
  return context;
};

export default TaskContext; 